.btn.btn-items {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  letter-spacing: 0;
  padding: 0;
  border: solid 1px #ff635c;
  text-align: center;
  color: #ff635c;
  flex-shrink: 0;
}

.input-label-absolute {
  position: relative;
}

.label-absolute {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #7f7f7f;
}

.input-label-absolute-left .label-absolute {
  left: .7rem;
}

.input-label-absolute-left .form-control {
  padding-left: 2rem;
}

.input-label-absolute-right .label-absolute {
  right: .7rem;
}

.input-label-absolute-right .form-control {
  padding-right: 2rem;
}

.input-expand {
  position: relative;
  z-index: 50;
}

.input-expand .form-control {
  width: 100%;
  max-width: 15rem;
  -webkit-transition: all .5s !important;
  transition: all .5s !important;
}

.input-expand.focus .form-control {
  width: 15rem !important;
}

.input-reset.focus .form-control {
  padding-right: 2rem;
}

.input-reset.focus .btn-reset {
  display: block;
}

.btn-reset {
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #7f7f7f;
  background: none;
}

.btn-reset:focus {
  box-shadow: none;
}

.btn-collapse::before {
  display: inline-block;
  height: 100%;
  margin-right: .5rem;
  content: '\f067';
  text-align: center;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
}

.btn-collapse[aria-expanded='true']::before {
  content: '\f068';
}

.btn-muted {
  color: #212529;
  background-color: #e4e4e4;
  border-color: #e4e4e4;
}

.btn-muted:hover {
  color: #212529;
  background-color: #d1d1d1;
  border-color: #cbcbcb;
}

.btn-muted:focus, .btn-muted.focus {
  box-shadow: 0 0 0 0.2rem rgba(199, 199, 200, 0.5);
}

.btn-muted.disabled, .btn-muted:disabled {
  color: #212529;
  background-color: #e4e4e4;
  border-color: #e4e4e4;
}

.btn-muted:not(:disabled):not(.disabled):active, .btn-muted:not(:disabled):not(.disabled).active,
.show > .btn-muted.dropdown-toggle {
  color: #212529;
  background-color: #cbcbcb;
  border-color: #c4c4c4;
}

.btn-muted:not(:disabled):not(.disabled):active:focus, .btn-muted:not(:disabled):not(.disabled).active:focus,
.show > .btn-muted.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(199, 199, 200, 0.5);
}

.btn-outline-muted {
  color: #e4e4e4;
  border-color: #e4e4e4;
}

.btn-outline-muted:hover {
  color: #212529;
  background-color: #e4e4e4;
  border-color: #e4e4e4;
}

.btn-outline-muted:focus, .btn-outline-muted.focus {
  box-shadow: 0 0 0 0.2rem rgba(228, 228, 228, 0.5);
}

.btn-outline-muted.disabled, .btn-outline-muted:disabled {
  color: #e4e4e4;
  background-color: transparent;
}

.btn-outline-muted:not(:disabled):not(.disabled):active, .btn-outline-muted:not(:disabled):not(.disabled).active,
.show > .btn-outline-muted.dropdown-toggle {
  color: #212529;
  background-color: #e4e4e4;
  border-color: #e4e4e4;
}

.btn-outline-muted:not(:disabled):not(.disabled):active:focus, .btn-outline-muted:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-muted.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(228, 228, 228, 0.5);
}

.custom-switch .custom-control-label {
