  position: absolute;
  border-radius: 100%;
  left: -16px;
  top: 47%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.filter-modal input:checked + .radio-button:before {
  border: 1px solid #ff635c;
}

