  margin: 0 !important;
  z-index: 100;
}

.selected-filters.fixed .selected-filters__separator {
  margin-top: 0 !important;
  opacity: 0;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.selected-filters.fixed .selected-filters__check {
