@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .ie-center,
  .hero-404,
  .hero-home,
  .hero-blog {
    position: relative;
    overflow: hidden;
  }
  .ie-center > *,
  .hero-404 > *,
  .hero-home > *,
  .hero-blog > * {
    position: absolute;
    top: 50%;
    left: 50%;
